import Vue from 'vue';
import VueRouter from 'vue-router';
import state from '@/store/state';
import mutations from '@/store/mutations';

Vue.use(VueRouter);

import App from '@/App';
import Login from '@/views/Login';
import Layout from '@/views/Layout';
import LayoutExternal from '@/views/LayoutExternal';

import { logout } from '@/utils/common.js';

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{ path: '/app/login', name: 'login', component: Login },
		{ path: '/app/layout', name: 'layout', component: Layout },
		{ path: '/app/layoutExternal', name: 'layoutExternal', component: LayoutExternal },
		{ path: '/', name: 'app', component: App }
	]
});

export default router;

router.beforeEach((to, from, next) => {
	if (`c` === to.query._client_mode) {
		if (to.path.startsWith(`/app/login`)) {
			logout(false);
		}

		sessionStorage.setItem(`externalMenuUrl`, to.path);

		mutations.setIsTools(state, true);
		window.isTools = true;

		const param = 0 !== Object.keys(to.query).filter(key => key !== `_client_mode`).length ? to.query : null;

		if (null !== param) {
			delete param['_client_mode'];

			sessionStorage.setItem(`externalMenuParam`, JSON.stringify(param));
			localStorage.setItem(`searchObj`, JSON.stringify(param));
		}
	} else {
		if (sessionStorage.getItem(`externalMenuUrl`) && !state.isTools) {
			mutations.setIsTools(state, true);
			window.isTools = true;
		}
	}

	next();
});
