<script>
import { HttpReq, escapeHtml, getHttpUrl } from '@/utils/common.js';

export default {
	name: `MigrationMake`,
	props: {
		detailData: Object,
		viewer: Boolean,
		editor: Boolean,
		migrationDataKey: String,
		migrationUrl: String,
		fileUpDownUrl: String
	},
	data() {
		return {
			userId: null,
			name: null,
			migrationDate: null,
			migrationTime: null,
			[this.migrationDataKey]: [],
			targetIds: null
		};
	},
	watch: {
		detailData: {
			deep: true,
			handler() {
				this.userId = null;
				this.name = null;
				this.migrationDate = null;
				this.migrationTime = null;
				this[this.migrationDataKey] = [];
				this.targetIds = null;

				if (!this.detailData) return;

				const { userId, name, migrationDate, migrationTime, targetIds } = this.detailData;

				this.userId = userId;
				this.name = name;
				this.migrationDate = migrationDate;
				this.migrationTime = migrationTime;
				this.targetIds = targetIds;

				this[this.migrationDataKey] = this.detailData[this.migrationDataKey];
			}
		}
	},
	methods: {
		async migration() {
			const result = await new HttpReq(this.migrationUrl).create({ targetIds: this.targetIds }, true);

			if (`ok` !== result.data.result) {
				this.$alert({
					type: `warn`,
					message: `${escapeHtml(result.data.error[0].className)}<br/><br/>${escapeHtml(result.data.error[0].message)}`,
					isXSSMode: true
				});
			} else {
				this.$alert({
					type: `normal`,
					message: this.$t(`igate.migration.migration`)
				});
			}
		},
		downloadFile() {
			this.$startSpinner();

			const req = new XMLHttpRequest();
			req.open('POST', getHttpUrl(this.fileUpDownUrl));

			req.setRequestHeader(`Authorization`, localStorage.getItem('accessToken'));
			req.setRequestHeader(`X-iManager-Method`, `GET`);

			req.withCredentials = true;

			req.responseType = 'blob';

			req.send(
				JSON.stringify({
					targetIds: this.targetIds
				})
			);

			req.onload = async event => {
				this.$stopSpinner();

				const blob = req.response;

				const promise = new Promise(resolve => {
					const reader = new FileReader();

					reader.onload = () => {
						resolve(JSON.parse(reader.result));
					};

					reader.readAsText(blob);
				});

				const result = await promise;

				const file_name = result.name;

				if (0 >= blob.size || 200 !== event.target.status) {
					this.$alert({ type: 'warn', message: this.$t(`head.fail.notice`) });
					return;
				}

				if (window.navigator && window.navigator.msSaveOrOpenBlob) {
					window.navigator.msSaveOrOpenBlob(blob, file_name);
				} else {
					const link = document.createElement('a');
					link.href = window.URL.createObjectURL(blob);
					link.download = file_name;
					link.click();
					URL.revokeObjectURL(link.href);
					link.remove();
				}
			};
		}
	}
};
</script>
