<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/adapter/Detail.vue';
import { getPrivilege, HttpReq, escapeHtml } from '@/utils/common.js';

export default {
	name: 'Adapter',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	async mounted() {
		let result = await new HttpReq(`/api/page/properties`).read({ pk: { propertyId: 'List.Adapter.Queuemode' }, orderByKey: true });

		if (`ok` === result.data.result) this.queueModeList = result.data.object;

		result = await new HttpReq(`/api/page/properties`).read({ pk: { propertyId: 'List.Adapter.Type' }, orderByKey: true });

		if (`ok` === result.data.result) this.adapterTypeList = result.data.object;

		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => this.search(this.$refs.search.getSearchObj()));
		}

		if (localStorage.getItem('detailObj')) {
			this.$nextTick(() => {
				const detailObj = JSON.parse(localStorage.getItem(`detailObj`));
				detailObj.isExpandDetailView = true;

				localStorage.removeItem(`detailObj`);

				this.initDetail('detail', detailObj);
			});
		}
	},
	data() {
		return {
			adapterTypeList: [],
			queueModeList: [],

			cudUrl: '/api/entity/adapter/object',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'adapterId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'adapterName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'select',
					vModel: 'adapterType',
					label: this.$t('common.type'),
					optionInfo: {
						url: '/api/page/properties',
						params: {
							pk: {
								propertyId: 'List.Adapter.Type'
							},
							orderByKey: true
						},
						optionListName: 'adapterTypeList',
						optionFor: 'option in adapterTypeList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'select',
					vModel: 'queueMode',
					label: `${this.$t('head.queue')} ${this.$t('head.mode')}`,
					optionInfo: {
						url: '/api/page/properties',
						params: {
							pk: {
								propertyId: 'List.Adapter.Queuemode'
							},
							orderByKey: true
						},
						optionListName: 'queueModeList',
						optionFor: 'option in queueModeList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'text',
					vModel: 'adapterDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					placeholder: this.$t('head.searchCoditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'add', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/adapter/search`,
				totalCntUrl: `/api/entity/adapter/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'adapterId',
							header: this.$t('head.id'),
							width: `20%`
						},
						{
							name: 'adapterName',
							header: this.$t('head.name'),
							width: `30%`
						},
						{
							name: 'adapterType',
							header: this.$t('common.type'),
							align: `center`,
							width: `10%`,
							formatter: ({ row }) => {
								const findAdapterType = this.adapterTypeList.find(info => info.pk.propertyKey === row.adapterType);
								return findAdapterType ? escapeHtml(findAdapterType.propertyValue) : '';
							}
						},
						{
							name: 'queueMode',
							header: this.$t('head.queue') + ' ' + this.$t('head.mode'),
							align: 'center',
							width: `10%`,
							formatter: ({ row }) => {
								const findQueueMode = this.queueModeList.find(info => info.pk.propertyKey === row.queueMode);
								return findQueueMode ? escapeHtml(findQueueMode.propertyValue) : '';
							}
						},
						{
							name: 'adapterDesc',
							header: this.$t('head.description'),
							width: `30%`
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['adapterId'],
			controlUrl: '/api/entity/adapter/control',
			controlParams: detailData => {
				return {
					adapterId: detailData.adapterId,
					instance: null
				};
			},

			detailInfo: {},
			isDetailView: false,
			detailMode: null,

			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'guide', isUse: true, url: '/manual/Property Reference.htm#_topic_AdapterProperty' },
				{ id: 'externalGuide', isUse: true, url: '/manual/External Guide.htm' },
				{ id: 'dump', isUse: true, dumpUrl: `/api/entity/adapter/dump` },
				{ id: 'start', isUse: true },
				{ id: 'stop', isUse: true },
				{ id: 'reference', isUse: true, className: 'com.inzent.igate.repository.meta.Adapter' }
			],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/adapter/BasicInfo'
				},
				{
					type: 'bundle',
					label: `${this.$t('igate.adapter')} ${this.$t('igate.operation')}`,
					url: '/adapter/OperationInfo'
				},
				{
					type: 'bundle',
					label: `${this.$t('igate.adapter')} ${this.$t('head.property')}`,
					url: '/adapter/PropertyInfo'
				},
				{
					type: 'bundle',
					label: this.$t('igate.connector'),
					url: '/adapter/ConnectorInfo'
				},
				{
					type: 'bundle',
					label: this.$t('igate.adapter.queue.deploies'),
					url: '/adapter/DeploiesInfo'
				}
			]
		};
	}
};
</script>
