<template>
	<div class="wrap" @contextmenu="conveyEvent" @click="conveyEvent">
		<SideBar @loadContent="loadContent" />
		<Dispatch :uuid="uuid" :menuId="menuId" />
		<span class="license" ref="license" :style="licenseStyle" v-if="isViewLicense"> {{ $t(`msg.license.development`) }} </span>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import { getUUID, MenuClickEventBus, WindowResizeEventBus, MenuContextMenuEventBus, openNewTab, HttpReq, getCurrentMenuId } from '@/utils/common.js';
import SideBar from '@/views/SideBar.vue';
import Dispatch from '@/components/main/Dispatch.vue';

export default {
	name: 'Layout',
	components: {
		SideBar,
		Dispatch
	},
	data() {
		return {
			uuid: null,
			menuId: null,
			isViewLicense: false,
			licenseStyle: {
				left: null,
				top: null
			}
		};
	},
	computed: {
		...mapState([`developmentLicense`])
	},
	async mounted() {
		const res = await new HttpReq(`/api/page/licenseSerial`).read();

		if (`ok` !== res.data.result) return;

		this.isViewLicense = this.developmentLicense === res.data.object;

		WindowResizeEventBus.$on('MainResize', this.resize);

		this.$nextTick(this.resize);
	},
	methods: {
		loadContent(menuInfo) {
			if (`Y` === menuInfo.openWindow && `Y` !== localStorage.getItem('isOpenNewTab')) {
				openNewTab(getCurrentMenuId(), () => {
					localStorage.setItem('isOpenNewTab', 'Y');
				});
			} else {
				localStorage.removeItem('isOpenNewTab');
			}

			this.uuid = getUUID();
			this.menuId = menuInfo.menuId;

			const classList = [];

			document.body.classList.forEach(className => {
				if (`moon` === className) return;
				classList.push(className);
			});

			document.body.classList.remove(...classList);

			MenuClickEventBus.$emit('ModalDelete');
		},
		conveyEvent(evt) {
			MenuContextMenuEventBus.$emit('initContextMenu', evt);
		},
		resize() {
			this.licenseStyle.left = `calc(100% - ${this.$refs.license.offsetWidth}px)`;
			this.licenseStyle.top = `calc(100% - ${this.$refs.license.offsetHeight}px)`;
		}
	}
};
</script>
<style scoped>
.license {
	position: absolute;
	color: #000;
	z-index: 999;
	font-size: 15px;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}
</style>
