<script>
import { getHttpUrl } from '@/utils/common.js';

export default {
	name: `MigrationImport`,
	props: {
		detailData: Object,
		viewer: Boolean,
		editor: Boolean,
		migrationDataKey: String,
		fileUpDownUrl: String
	},
	data() {
		return {
			fileName: null,
			errorMessage: null,
			userId: null,
			name: null,
			migrationDate: null,
			migrationTime: null,
			[this.migrationDataKey]: [],
			fileData: null
		};
	},
	watch: {
		detailData: {
			deep: true,
			handler() {
				this.fileName = null;
				this.errorMessage = null;
				this.userId = null;
				this.name = null;
				this.migrationDate = null;
				this.migrationTime = null;
				this[this.migrationDataKey] = [];
				this.fileData = null;
			}
		}
	},
	methods: {
		selectFile() {
			const fileInput = document.createElement(`input`);

			fileInput.setAttribute(`type`, `file`);

			fileInput.addEventListener(`change`, evt => {
				this.fileData = evt.target.files[0];
				this.fileName = this.fileData.name;
			});

			fileInput.click();
		},
		async uploadFile() {
			if (null === this.fileData) {
				this.$alert({ type: `warn`, message: this.$t(`igate.migration.fileSelectError`) });
				return;
			}

			this.$startSpinner();

			const req = new XMLHttpRequest();
			req.open('POST', getHttpUrl(this.fileUpDownUrl), true);

			req.setRequestHeader(`Authorization`, localStorage.getItem('accessToken'));
			req.setRequestHeader(`X-iManager-Method`, `POST`);

			req.withCredentials = true;

			const formData = new FormData();
			formData.enctype = `multipart/form-data`;
			formData.append(`uploadFile`, this.fileData);

			req.send(formData);

			req.onload = () => {
				this.$stopSpinner();

				const res = JSON.parse(req.response);

				if (`ok` !== res.result) {
					this.$alert({
						type: `warn`,
						message: `${this.$t(`head.upload`)} ${this.$t(`head.fail`)}`
					});

					this.errorMessage = `${res.error[0].className} ${res.error[0].message}`;
				} else {
					this.$alert({
						type: `normal`,
						message: `${this.$t(`head.upload`)} ${this.$t(`head.success`)}`
					});

					this.errorMessage = null;

					if (res.object) {
						const { userId, name, migrationDate, migrationTime } = res.object;

						this.userId = userId;
						this.name = name;
						this.migrationDate = migrationDate;
						this.migrationTime = migrationTime;
						this[this.migrationDataKey] = res.object[this.migrationDataKey];
					}
				}
			};
		}
	}
};
</script>
