<template>
	<div id="panel" ref="panel" :class="panelClass">
		<div class="panel-dialog">
			<div class="panel-content" :style="panelContentStyle">
				<div class="resize-bar"></div>

				<header id="panel-header" class="panel-header sub-bar">
					<a
						href="javascript:void(0);"
						class="btn btn-icon backBtn"
						:title="$t('head.goBackward')"
						@click="goPreviousPanel"
						v-if="'modify' === panelMode"
						><i class="icon-left"></i>
					</a>
					<h2 class="sub-bar-tit" :style="{ width: `calc(100% - ${'modify' === panelMode ? 170 : 125}px )` }">
						{{ subBarTitle }}
						<span
							class="sub-bar-selected-tit ellipsis"
							:title="selectedInfoTitle ? selectedInfoTitle.substr(3) : ``"
							:style="{ width: `calc(100% - ${'modify' === panelMode ? 80 : 65}px )` }"
						>
							{{ selectedInfoTitle }}
						</span>
					</h2>
					<div class="ml-auto">
						<button
							type="button"
							class="btn-change-layout-horizon btn-icon"
							@click="changeLayout(`vertical`)"
							:title="$t(`common.detail.horizontal.view`)"
						>
							<img :src="getMenuIconUrl(`horizontal`)" class="center-block" />
						</button>
						<button
							type="button"
							class="btn-change-layout-vertical btn-icon"
							@click="changeLayout(`horizon`)"
							:title="$t(`common.detail.vertical.view`)"
						>
							<img :src="getMenuIconUrl(`vertical`)" class="center-block" />
						</button>
						<button type="button" class="btn-icon" @click="expandCollapsePanel" :title="$t('head.expand')">
							<i class="icon-expand"></i>
						</button>
						<button type="button" class="btn-icon" @click="closePanel" :title="$t('head.close')"><i class="icon-close"></i></button>
					</div>
				</header>

				<ul class="nav nav-tabs flex-shrink-0">
					<li class="nav-item" v-for="(tabObj, index) in tabList" :key="tabObj.id">
						<a
							href="javascript:void(0);"
							:class="{ 'nav-link': true, active: selectedIdx === index }"
							@click="changeTab(index)"
							v-text="tabObj.label"
						>
						</a>
					</li>
					<li class="nav-item" v-if="toShowResult">
						<a
							href="javascript:void(0);"
							:class="{ 'nav-link': true, active: selectedIdx === tabList.length }"
							@click="changeTab(tabList.length)"
						>
							{{ $t('head.executionResult') }}
						</a>
					</li>
				</ul>
				<!-- tab-pane -->
				<div class="panel-body tab-content">
					<div
						:class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === idx }"
						v-for="(detailInfo, idx) in tabList"
						:key="idx"
					>
						<DetailBasic
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-if="'basic' === detailInfo.type"
						/>
						<DetailProperty
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'property' === detailInfo.type"
						/>
						<DetailGrid
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'grid' === detailInfo.type"
						/>
						<DetailCustom
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'custom' === detailInfo.type"
						/>
						<component
							ref="component"
							:is="require(`@/views/detail${detailInfo.url}.vue`).default"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'bundle' === detailInfo.type"
						/>
					</div>
					<DetailResult
						:class="{ 'tab-pane': true, active: selectedIdx === tabList.length && showResultTab }"
						:httpReqResult="httpReqResult"
						v-if="toShowResult"
					/>
				</div>

				<footer id="panel-footer" class="panel-footer sub-bar">
					<div class="ml-auto" v-if="'detail' === panelMode || 'done' === panelMode">
						<a href="javascript:void(0);" class="btn" @click="goMetaHistoryModal" v-if="metaHistoryBtn && viewer">
							<i class="icon-eye"></i>{{ $t('common.metaHistory') }}
						</a>
						<a
							href="javascript:void(0);"
							class="btn"
							@click="goReferenceModal"
							v-if="referenceBtn && viewer"
							:title="$t('head.referenceTreeInfo')"
						>
							<i class="icon-result"></i><span class="hide">{{ $t('head.referenceTreeInfo') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-primary"
							@click="excelExport"
							v-if="excelExportBtn && viewer"
							:title="$t('head.excel.output')"
						>
							<i class="icon-result"></i><span class="hide">{{ $t('head.excel.output') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn" @click="guide" v-if="guideBtn && editor" :title="$t('igate.connector.guide')">
							{{ $t('igate.connector.guide') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="control(`load`)" v-if="loadBtn && editor" :title="$t('head.load')">
							{{ $t('head.load') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="dump" v-if="dumpBtn && editor" :title="$t('head.dump')">
							{{ $t('head.dump') }}
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-m"
							@click="control(`execute`)"
							v-if="executeBtn && editor"
							:title="$t('head.execute')"
						>
							<i class="icon-play"></i><span class="hide">{{ $t('head.execute') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-m"
							@click="control(`start`)"
							v-if="startBtn && editor"
							:title="$t('head.execute')"
						>
							<i class="icon-play"></i><span class="hide">{{ $t('head.execute') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn btn-m" @click="control(`stop`)" v-if="stopBtn && editor" :title="$t('head.stop')">
							<i class="icon-pause"></i><span class="hide">{{ $t('head.stop') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn btn-m" @click="control(`stop`)" v-if="stopBtn && editor" :title="$t('head.stop')">
							<i class="icon-pause"></i><span class="hide">{{ $t('head.stop') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-m"
							@click="control(`stopForce`)"
							v-if="stopForceBtn && editor"
							:title="$t('head.stop.force')"
						>
							<i class="icon-x"></i><span class="hide">{{ $t('head.stop.force') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn"
							@click="control(`interrupt`)"
							v-if="interruptBtn && editor"
							:title="$t('head.interrupt')"
						>
							<span class="hide">{{ $t('head.interrupt') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn" @click="control(`block`)" v-if="blockBtn && editor" :title="$t('head.block')">
							<span class="hide">{{ $t('head.block') }}</span>
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-m"
							@click="control(`unblock`)"
							v-if="unblockBtn && editor"
							:title="$t('head.unblock')"
						>
							<span class="hide">{{ $t('head.unblock') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn" @click="removeInfo" v-if="deleteBtn && editor" :title="$t('head.delete')">
							<i class="icon-delete"></i>{{ $t('head.delete') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="goModifyPanel" v-if="updateBtn && editor" :title="$t('head.update')">
							<i class="icon-edit"></i>{{ $t('head.update') }}
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-primary"
							@click="goAddInfo"
							v-if="insertBtn && editor"
							:title="`${$t('head.insert')} (${$t('head.copy')})`"
						>
							<i class="icon-plus"></i>{{ `${$t('head.insert')} (${$t('head.copy')})` }}
						</a>
						<a
							href="javascript:void(0);"
							class="btn btn-primary"
							@click="restoreMeta"
							v-if="restoreBtn && editor"
							:title="$t('head.restore')"
						>
							<i class="icon-edit"></i>{{ $t('head.restore') }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'add' === panelMode || 'clone' === panelMode">
						<a href="javascript:void(0);" class="btn btn-primary" @click="saveInfo" v-if="insertBtn && editor" :title="$t('head.insert')">
							<i class="icon-plus"></i>{{ $t('head.insert') }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'modify' === panelMode">
						<a
							href="javascript:void(0);"
							class="btn btn-primary"
							@click="updateInfo"
							v-if="updateBtn && editor"
							:title="$t('head.update')"
						>
							<i class="icon-edit"></i>{{ $t('head.update') }}
						</a>
					</div>
				</footer>
			</div>
		</div>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import merge from 'lodash/merge';
import { mapState } from 'vuex';
import {
	WindowResizeEventBus,
	DetailToggleEventBus,
	parseFlattenObj,
	HttpReq,
	getHttpUrl,
	getNumFromStr,
	parseHierarchyObj,
	getCurrentMenuId
} from '@/utils/common.js';
import DetailBasic from '@/components/detail/DetailBasic';
import DetailProperty from '@/components/detail/DetailProperty';
import DetailResult from '@/components/detail/DetailResult';
import DetailGrid from '@/components/detail/DetailGrid';
import DetailCustom from '@/components/detail/DetailCustom';

export default {
	name: 'Detail',
	props: {
		mode: {
			type: String,
			default: 'detail'
		},
		pk: Array,
		buttonList: Array,
		detailTabList: {
			type: Array,
			default: () => []
		},
		info: Object,
		cudUrl: String,
		controlUrl: String,
		controlParams: Function,
		viewer: Boolean,
		editor: Boolean
	},
	computed: {
		...mapState([`detailConstants`, `constantsInfo`]),
		toShowResult() {
			return (this.insertBtn || this.updateBtn || this.deleteBtn || this.dumpBtn || this.restoreBtn) && this.editor;
		}
	},
	components: {
		DetailBasic,
		DetailProperty,
		DetailGrid,
		DetailCustom,
		DetailResult
	},
	data() {
		return {
			panelMode: this.mode,
			selectedIdx: 0,
			subBarTitle: '',

			tabList: [],
			tabAllData: {},

			insertBtn: false,
			updateBtn: false,
			deleteBtn: false,
			restoreBtn: false,
			excelExportBtn: false,
			dumpBtn: false,
			loadBtn: false,
			executeBtn: false,
			guideBtn: false,

			startBtn: false,
			stopBtn: false,
			interruptBtn: false,
			stopForceBtn: false,
			blockBtn: false,
			unblockBtn: false,
			reorderBtn: false,
			referenceBtn: false,

			metaHistoryBtn: false,

			httpReq: null,
			httpReqResult: {},
			showResultTab: false,

			detailData: {},

			cloneInfo: {},

			panelClass: {
				panel: true,
				'panel-bottom': true,
				horizon: false,
				vertical: false,
				expand: false
			},
			panelContentStyle: {
				width: null,
				height: null,
				'margin-left': null
			},
			beforeDragEvt: null,
			screenType: null,
			menuId: null,

			selectedInfoTitle: null
		};
	},
	watch: {
		cudUrl: {
			immediate: true,
			handler() {
				this.httpReq = new HttpReq(this.cudUrl);
			}
		},
		detailTabList: {
			immediate: true,
			handler() {
				if (0 === this.detailTabList.length) return;
				this.tabList = this.constantsInfo.detail.tabListFunc(getCurrentMenuId(), cloneDeep(this.detailTabList));
			}
		},
		buttonList: {
			immediate: true,
			handler() {
				this.setButtonList();
			}
		},
		mode() {
			this.panelMode = this.mode;
		},
		panelMode() {
			this.$nextTick(() => {
				if (null === this.$el.querySelector(`.nav-link.active`)) {
					this.changeTab(0);
				}

				this.$el.querySelectorAll(`.panel-footer .btn`).forEach(element => {
					element.blur();
				});

				//watch cloneInfo
				this.getTargetObj();
			});
		},
		info() {
			if (!this.info) return;

			this.cloneInfo = parseFlattenObj(this.info);

			if (this.info.isNewTab) {
				this.expandCollapsePanel();
				delete this.cloneInfo.isNewTab;
			}

			if (this.info.isExpandDetailView) {
				delete this.cloneInfo.isExpandDetailView;
				this.panelClass.expand = true;
			}
		}
	},
	mounted() {
		this.menuId = getCurrentMenuId();

		document.body.addEventListener(`mousedown`, this.mousedown);
		document.body.addEventListener(`resize`, this.bodyResize);
	},
	destroyed() {
		document.body.removeEventListener(`mousedown`, this.mousedown);
		document.body.removeEventListener(`resize`, this.bodyResize);
	},
	methods: {
		async getTargetObj() {
			if (null === this.panelMode) {
				return;
			}

			if ('done' !== this.panelMode) {
				this.detailData = {};

				if ('add' !== this.panelMode) {
					const param = {};

					for (const key in this.cloneInfo) {
						if (-1 === this.pk.indexOf(key)) continue;
						merge(param, parseHierarchyObj({ [key]: this.cloneInfo[key] }));
					}

					const result = await this.httpReq.read(param);

					if (`ok` !== result.data.result) return;

					this.detailData = parseFlattenObj(result.data.object);

					// if ('clone' === this.panelMode) {
					// 	this.pk.forEach(pkInfo => {
					// 		this.detailData[pkInfo] = null;
					// 	});
					// }
				}
			}

			this.setSelectedInfoTitle();

			this.setButtonList();

			this.openPanel();
		},
		changeTab(idx) {
			this.selectedIdx = idx;

			this.$nextTick(() => {
				DetailToggleEventBus.$emit('MainResize');
			});
		},
		setButtonList() {
			this.buttonList.forEach(info => {
				this[`${info.id}Btn`] = 'function' === typeof info.isUse ? info.isUse(this.detailData) : info.isUse;
			});
		},
		initDefaultDetailLayoutInfo() {
			this.panelClass.vertical = true;
			this.panelClass.horizon = false;
			this.panelClass.expand = this.constantsInfo.isDetailExpand;

			this.panelContentStyle.width = null;
			this.panelContentStyle.height = null;
			this.panelContentStyle[`margin-left`] = null;

			localStorage.setItem(
				`detailLayoutInfo_${this.menuId}`,
				JSON.stringify({
					panelLayoutDirection: `vertical`,
					panelContentStyle: this.panelContentStyle,
					screenType: this.screenType,
					expand: this.panelClass.expand
				})
			);
		},
		openPanel() {
			const subBarTitleMsg = {
				detail: 'Detail',
				done: 'Detail',
				add: 'Insert',
				clone: 'Insert',
				modify: 'Update'
			};

			this.subBarTitle = subBarTitleMsg[this.panelMode];

			if ('done' !== this.panelMode && this.showResultTab) {
				this.selectedIdx = 0;
				this.showResultTab = false;
			}

			if (document.body.classList.contains('fixed')) {
				document.body.classList.remove(
					'panel-open-detail',
					'panel-open-add',
					'panel-open-modify',
					'panel-open-done',
					'panel-open-clone',
					'fixed'
				);

				this.$refs.panel.style.display = 'none';
			}

			document.body.classList.add('fixed');

			this.$refs.panel.style.display = 'block';

			document.body.classList.add(`panel-open-${this.panelMode}`);

			this.screenType = 991 < window.innerWidth ? `pc` : 767 < window.innerWidth ? `tablet` : `mobile`;

			if (localStorage.getItem(`detailLayoutInfo_${this.menuId}`)) {
				const { panelLayoutDirection, panelContentStyle, screenType, expand } = JSON.parse(
					localStorage.getItem(`detailLayoutInfo_${this.menuId}`)
				);

				if (screenType !== this.screenType) {
					this.initDefaultDetailLayoutInfo();
				} else {
					this.panelClass.horizon = `horizon` === panelLayoutDirection;
					this.panelClass.vertical = `vertical` === panelLayoutDirection;
					this.panelClass.expand = expand;
					this.panelContentStyle = panelContentStyle;
				}
			} else {
				this.initDefaultDetailLayoutInfo();
			}

			this.$nextTick(() => {
				DetailToggleEventBus.$emit('MainResize');

				if (this.panelClass.horizon)
					this.$emit(`detailHorizonResize`, { detailWidth: document.querySelector(`#panel .panel-content`).clientWidth });
			});
		},
		closePanel() {
			document.body.classList.remove(`panel-open-${this.panelMode}`);

			this.$refs.panel.style.display = 'none';
			document.body.classList.remove('fixed');

			this.$refs.panel.removeAttribute('style');

			DetailToggleEventBus.$emit('MainResize');

			this.$nextTick(() => {
				if (this.panelClass.horizon) this.$emit(`detailHorizonResize`, { detailWidth: null });
			});
		},
		expandCollapsePanel(evt) {
			this.beforeDragEvt = null;

			this.panelContentStyle.width = null;
			this.panelContentStyle[`margin-left`] = null;
			this.panelContentStyle.height = null;

			this.panelClass.expand = !this.panelClass.expand;

			localStorage.setItem(
				`detailLayoutInfo_${this.menuId}`,
				JSON.stringify({
					panelLayoutDirection: this.panelClass.horizon ? `horizon` : `vertical`,
					panelContentStyle: this.panelContentStyle,
					screenType: this.screenType,
					expand: this.panelClass.expand
				})
			);

			DetailToggleEventBus.$emit('MainResize');

			document.body.dispatchEvent(
				new CustomEvent('resize', {
					detail: {
						evt
					}
				})
			);
		},
		goPreviousPanel() {
			this.panelMode = 'detail';
		},
		goAddInfo() {
			this.panelMode = 'clone';
		},
		goModifyPanel() {
			this.panelMode = 'modify';
		},
		removeInfo() {
			this.$confirm({
				type: 'warn',
				message: this.$t('head.delete.conform'),
				callback: () => {
					this.loadData(async () => {
						this.pk.forEach(pkInfo => {
							if ('undefined' === typeof this.tabAllData[pkInfo]) {
								this.tabAllData[pkInfo] = this.cloneInfo[pkInfo];
							}
						});

						const param = {};

						for (const key in this.tabAllData) {
							if (-1 === this.pk.indexOf(key)) continue;
							merge(param, parseHierarchyObj({ [key]: this.cloneInfo[key] }));
						}

						const result = await this.httpReq.delete(param, true);
						this.setData(result, 'head.delete.notice', this.closePanel, `delete`);
					});
				}
			});
		},
		saveInfo() {
			this.loadData(async () => {
				this.pk.forEach(pkName => {
					if ('undefined' === typeof this.tabAllData[pkName]) {
						merge(this.tabAllData, parseHierarchyObj({ [pkName]: '' }));
					}
				});

				const result = await this.httpReq.create(this.tabAllData, true);
				this.setData(result, 'head.insert.notice');
			});
		},
		updateInfo() {
			this.loadData(async () => {
				this.pk.forEach(pkName => {
					if ('undefined' === typeof this.tabAllData[pkName]) {
						merge(this.tabAllData, parseHierarchyObj({ [pkName]: this.cloneInfo[pkName] }));
					}
				});

				const result = await this.httpReq.update(this.tabAllData, true);
				this.setData(result, 'head.update.notice');
			});
		},
		async loadData(callBack) {
			this.$refs.component.forEach((comp, idx) => {
				if ('custom' !== this.tabList[idx].type && comp.getData) {
					this.tabAllData = merge(this.tabAllData, comp.getData());
				}
			});

			if (-1 === this.tabList.findIndex(tabInfo => 'custom' === tabInfo.type)) {
				if (callBack) callBack();
				return;
			}

			//get only custom data
			this.$startSpinner();

			const promiseAllList = [];

			this.$refs.component.forEach((comp, idx) => {
				if ('custom' !== this.tabList[idx].type) return;

				promiseAllList.push(
					new Promise(resolve => {
						const elementDiv = comp.$el.querySelector('[data-ready]');

						const customDataCallBack = e => {
							this.tabAllData = merge(this.tabAllData, e.detail);
							elementDiv.removeEventListener('setCustomData', customDataCallBack);
							resolve(idx);
						};

						elementDiv.addEventListener('setCustomData', customDataCallBack);

						comp.getData();
					})
				);
			});

			try {
				await Promise.all(promiseAllList);
				if (callBack) callBack();
			} catch (error) {
				this.$alert({ type: 'warn', message: this.$t('dashboard.noMatch.criteria') });
			} finally {
				this.$stopSpinner();
			}
		},
		async setData(result, message, successCallback, type = null, isSearch = true) {
			const resultState = 'ok' === result.data.result;

			this.httpReqResult = {
				state: resultState,
				content: resultState ? result.data.response : result.data.error
			};

			this.selectedIdx = this.tabList.length;
			this.showResultTab = true;

			if (!resultState) {
				this.tabAllData = {};
				return;
			}

			let info = null;

			if (result.data.object) {
				info = result;
			} else {
				const param = {};

				for (const key in this.tabAllData) {
					if (-1 === this.pk.indexOf(key)) continue;
					merge(param, parseHierarchyObj({ [key]: this.tabAllData[key] }));
				}

				if (0 === Object.keys(param).length) {
					for (const key in this.detailData) {
						if (-1 === this.pk.indexOf(key)) continue;
						merge(param, parseHierarchyObj({ [key]: this.detailData[key] }));
					}
				}

				info = await this.httpReq.read(param);
			}

			this.tabAllData = {};

			if (`ok` === info.data.result) {
				this.detailData = parseFlattenObj(cloneDeep(info.data.object));

				if ('add' === this.panelMode || 'clone' === this.panelMode) {
					this.cloneInfo = parseFlattenObj(cloneDeep(info.data.object));
				}
			}

			if (`delete` !== type) this.panelMode = 'done';

			if (message) this.$alert({ type: 'normal', message: this.$t(message) });

			if (isSearch) this.$emit('search');

			if (successCallback) successCallback();
		},
		async excelExport() {
			const exportBtnInfo = this.buttonList.find(btn => 'excelExport' === btn.id);

			if (!exportBtnInfo) return;

			this.$startSpinner();

			const req = new XMLHttpRequest();
			req.open('POST', getHttpUrl(exportBtnInfo.exportUrl), true);

			req.setRequestHeader(`Authorization`, localStorage.getItem('accessToken'));
			req.setRequestHeader(`X-iManager-Method`, `GET`);

			req.withCredentials = true;

			req.responseType = 'blob';

			const param = {};

			for (const key of this.pk) {
				param[key] = this.cloneInfo[key];
			}

			req.send(JSON.stringify(param));

			req.onload = event => {
				this.$stopSpinner();

				const blob = req.response;
				const file_name = exportBtnInfo.fileName ? `${exportBtnInfo.fileName}_${Date.now()}.xlsx` : `ExcelExport_${Date.now()}.xlsx`;

				if (blob.size <= 0 || event.target.status != '200') {
					this.$alert({ type: 'warn', message: this.$t(`head.fail.notice`) });
					return;
				}

				if (window.navigator && window.navigator.msSaveOrOpenBlob) {
					window.navigator.msSaveOrOpenBlob(blob, file_name);
				} else {
					const link = document.createElement('a');
					link.href = window.URL.createObjectURL(blob);
					link.download = file_name;
					link.click();
					URL.revokeObjectURL(link.href);
					link.remove();
				}
			};
		},
		restoreMeta() {
			this.loadData(async () => {
				const result = await new HttpReq('/api/entity/metaHistory/restore').create(this.detailData, true);
				this.setData(result, 'head.restore.notice');
			});
		},
		async control(mod) {
			const params = this.controlParams(parseHierarchyObj(this.detailData));
			this.setData(await new HttpReq(`${this.controlUrl}?command=${mod}`).create(params, true), null, null, null, false);
		},
		async dump() {
			const dumpBtnInfo = this.buttonList.find(btn => 'dump' === btn.id);

			const params = this.controlParams(parseHierarchyObj(this.detailData));

			this.setData(await new HttpReq(dumpBtnInfo.dumpUrl).read(params, true), null, null, null, false);
		},
		guide() {
			const guideBtnInfo = this.buttonList.find(btn => 'guide' === btn.id);

			if (!guideBtnInfo) return;

			window.open(
				getHttpUrl(guideBtnInfo.url),
				'_blank',
				'height=886, width=785,resizable=yes, toolbar=no, menubar=no, location=no, scrollbars=yes, status=no'
			);
		},
		bodyResize() {
			let isChangeVerticalLayout = false;

			if (991 < window.innerWidth) {
				if (`pc` !== this.screenType) isChangeVerticalLayout = true;
				else if (this.panelClass.horizon) {
					this.beforeDragEvt = null;

					if (this.panelContentStyle.width && document.body.clientWidth - 240 <= getNumFromStr(this.panelContentStyle.width)) {
						this.panelContentStyle.width = `${document.body.clientWidth - 240}px`;
						this.panelContentStyle[`margin-left`] = `calc(100% - ${this.panelContentStyle.width})`;

						localStorage.setItem(
							`detailLayoutInfo_${this.menuId}`,
							JSON.stringify({
								panelLayoutDirection: `horizon`,
								panelContentStyle: this.panelContentStyle,
								screenType: this.screenType,
								expand: this.panelClass.expand
							})
						);
					}

					this.$emit(`detailHorizonResize`, { detailWidth: document.querySelector(`#panel .panel-content`).clientWidth });
				}
			} else {
				if (this.panelClass.horizon) isChangeVerticalLayout = true;
				else if (767 < window.innerWidth) isChangeVerticalLayout = `tablet` !== this.screenType;
				else if (767 >= window.innerWidth) isChangeVerticalLayout = `mobile` !== this.screenType;
			}

			this.screenType = 991 < window.innerWidth ? `pc` : 767 < window.innerWidth ? `tablet` : `mobile`;

			if (isChangeVerticalLayout) this.changeLayout(`vertical`);

			if (this.panelClass.vertical) {
				if (document.body.clientHeight < document.querySelector(`#panel .panel-content`).clientHeight) {
					this.changeLayout(`vertical`);
				}
			}
		},
		mousedown(evt) {
			if (!evt.target.classList.contains('resize-bar')) return;

			const mouseup = () => {
				document.body.removeEventListener(`mousemove`, mousemove);
				document.body.removeEventListener(`mouseup`, mouseup);
				document.body.removeEventListener(`mouseleave`, mouseleave);
			};

			const mousemove = evt => {
				this.detailResize(evt);
				evt.preventDefault();
			};

			const mouseleave = () => {
				document.body.removeEventListener(`mousemove`, mousemove);
				document.body.removeEventListener(`mouseup`, mouseup);
				document.body.removeEventListener(`mouseleave`, mouseleave);
			};

			document.body.addEventListener(`mousemove`, mousemove);
			document.body.addEventListener(`mouseup`, mouseup);
			document.body.addEventListener(`mouseleave`, mouseleave);
		},
		detailResize(evt) {
			if (this.panelClass.vertical) {
				if (0 === evt.clientY) return;

				if (null !== this.panelContentStyle.width) this.panelContentStyle.width = null;
				if (null !== this.panelContentStyle[`margin-left`]) this.panelContentStyle[`margin-left`] = null;

				if (!this.beforeDragEvt) {
					this.beforeDragEvt = evt;
					this.panelContentStyle.height = `${document.querySelector(`#panel .panel-content`).clientHeight}px`;
				} else if (evt.clientY < this.beforeDragEvt.clientY) {
					this.panelContentStyle.height = `${getNumFromStr(this.panelContentStyle.height) + (this.beforeDragEvt.clientY - evt.clientY)}px`;
				} else {
					this.panelContentStyle.height = `${getNumFromStr(this.panelContentStyle.height) - (evt.clientY - this.beforeDragEvt.clientY)}px`;
				}

				if (document.body.clientHeight - (992 <= window.innerWidth ? 72 : 50) <= getNumFromStr(this.panelContentStyle.height)) {
					this.panelContentStyle.height = `${document.body.clientHeight - (992 <= window.innerWidth ? 72 : 50)}px`;
					return;
				}

				if (getNumFromStr(this.panelContentStyle.height) < 100) {
					this.panelContentStyle.height = `100px`;
					return;
				}
			} else {
				if (0 === evt.clientX) return;

				if (null !== this.panelContentStyle.height) {
					this.panelContentStyle.height = null;
				}

				if (!this.beforeDragEvt) {
					this.beforeDragEvt = evt;
					this.panelContentStyle.width = `${document.querySelector(`#panel .panel-content`).clientWidth}px`;
				} else if (evt.clientX < this.beforeDragEvt.clientX) {
					this.panelContentStyle.width = `${getNumFromStr(this.panelContentStyle.width) + (this.beforeDragEvt.clientX - evt.clientX)}px`;
				} else {
					this.panelContentStyle.width = `${getNumFromStr(this.panelContentStyle.width) - (evt.clientX - this.beforeDragEvt.clientX)}px`;
				}

				if (992 <= window.innerWidth && document.getElementById('sidebar') && !document.body.classList.contains('sidebar-toggled')) {
					if (document.body.clientWidth - 240 <= getNumFromStr(this.panelContentStyle.width)) {
						this.panelContentStyle.width = `${document.body.clientWidth - 240}px`;
						return;
					}

					if (getNumFromStr(this.panelContentStyle.width) < 240) {
						this.panelContentStyle.width = `240px`;
						return;
					}
				}

				this.panelContentStyle[`margin-left`] = `calc(100% - ${this.panelContentStyle.width})`;

				this.$emit(`detailHorizonResize`, { detailWidth: getNumFromStr(this.panelContentStyle.width) });
			}

			this.beforeDragEvt = evt;

			WindowResizeEventBus.$emit(`MainResize`);

			this.panelClass.expand = false;

			localStorage.setItem(
				`detailLayoutInfo_${this.menuId}`,
				JSON.stringify({
					panelLayoutDirection: this.panelClass.horizon ? `horizon` : `vertical`,
					panelContentStyle: this.panelContentStyle,
					screenType: this.screenType,
					expand: this.panelClass.expand
				})
			);
		},
		changeLayout(type) {
			this.panelClass.horizon = `horizon` === type;
			this.panelClass.vertical = `vertical` === type;
			this.panelClass.expand = this.constantsInfo.isDetailExpand;

			this.panelContentStyle.width = null;
			this.panelContentStyle.height = null;
			this.panelContentStyle[`margin-left`] = null;

			localStorage.setItem(
				`detailLayoutInfo_${this.menuId}`,
				JSON.stringify({
					panelLayoutDirection: this.panelClass.horizon ? `horizon` : `vertical`,
					panelContentStyle: this.panelContentStyle,
					screenType: this.screenType,
					expand: this.panelClass.expand
				})
			);

			this.beforeDragEvt = null;

			this.$nextTick(() => {
				this.$emit(`detailHorizonResize`, {
					detailWidth: this.panelClass.horizon ? document.querySelector(`#panel .panel-content`).clientWidth : null
				});
			});
		},
		getMenuIconUrl(menuIconName) {
			return getHttpUrl(`/custom/img/${menuIconName}.svg`);
		},
		setSelectedInfoTitle() {
			if ([`modify`, `detail`, `done`].find(panelMode => panelMode === this.panelMode)) {
				const menuDetailConstants = this.detailConstants.selectedInfoTitleKey;

				const selectedInfoTitleKey = menuDetailConstants && menuDetailConstants[this.menuId] ? menuDetailConstants[this.menuId] : this.pk;

				this.selectedInfoTitle = ``;

				selectedInfoTitleKey.forEach(key => {
					if (`undefined` === typeof this.detailData[key]) return;

					if (0 === String(this.detailData[key]).trim().length) return;

					this.selectedInfoTitle += String(this.detailData[key]);

					if (1 < selectedInfoTitleKey.length) this.selectedInfoTitle += `, `;
				});

				if (0 < this.selectedInfoTitle.length) {
					let selectedInfoTitle = this.selectedInfoTitle.trim();

					if (selectedInfoTitle.endsWith(`,`)) {
						selectedInfoTitle = selectedInfoTitle.substring(0, selectedInfoTitle.length - 1);
					}

					this.selectedInfoTitle = ` - ` + selectedInfoTitle;
				}
			} else {
				this.selectedInfoTitle = ``;
			}
		},
		goMetaHistoryModal() {
			const _this = this;

			const { className, entityIdKey } = this.buttonList.find(btn => 'metaHistory' === btn.id);

			let entityId = ``;

			if (Array.isArray(entityIdKey)) {
				entityIdKey.forEach(key => {
					entityId += `[${this.detailData[key]}]`;
				});
			} else {
				entityId += this.detailData[entityIdKey];
			}

			const metaHistoryModalInstance = this.$openModal({
				title: this.$t(`common.metaHistory`),
				modalWidth: `1280px`,
				modalHeight: `800px`,
				contentList: [
					{
						template: `<div>
								       <Search :list="searchList" @search="search" ref="search"/>
									   <Main
										 :buttonList="buttonList"
										 :gridInfo="gridInfo"
										 :gridSearchObj="gridSearchObj"
										 :gridType="'modalBasic'"
										 :gridResizeFunc="resize"
										 :viewer="true"
										 @rowClicked="rowClicked"
										 @initialize="initialize"
									   />
								   </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{ type: 'singleDaterange', vModel: 'modifyDateTimeFrom', label: this.$t('head.from'), dateRangeType: 'from' },
									{ type: 'singleDaterange', vModel: 'modifyDateTimeTo', label: this.$t('head.to'), dateRangeType: 'to' },
									{
										type: 'text',
										vModel: 'entityId',
										label: this.$t('common.metaHistory.entityId'),
										placeholder: this.$t('head.searchId'),
										val: entityId,
										readonly: true
									},
									{
										type: 'select',
										vModel: 'modifyType',
										label: this.$t('common.metaHistory.modifyType'),
										optionInfo: {
											url: '/api/page/properties',
											params: {
												pk: {
													propertyId: 'List.MetaHistory.Type'
												},
												orderByKey: true
											},
											optionListName: 'metaHistoryTypes',
											optionFor: 'option in metaHistoryTypes',
											optionValue: 'option.pk.propertyKey',
											optionText: 'option.propertyValue'
										}
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/api/entity/metaHistory/search',
									totalCntUrl: '/api/entity/metaHistory/count',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('head.update.timestamp'),
												name: 'pk.modifyDateTime',
												width: '35%',
												align: 'center',
												formatter: obj => obj.value.substring(0, 19)
											},
											{
												header: `Entity Version`,
												name: 'entityVersion',
												width: '10%',
												align: `right`
											},
											{
												header: this.$t('common.metaHistory.modifyType'),
												name: 'modifyType',
												width: '20%',
												align: 'center',
												formatter: value => {
													const modifyType = value.row.modifyType;

													return 'D' == modifyType
														? this.$t('head.delete')
														: 'I' == modifyType
														? this.$t('head.insert')
														: 'R' == modifyType
														? this.$t('common.metaHistory.restore')
														: 'U' == modifyType
														? this.$t('head.update')
														: '';
												}
											},
											{
												header: this.$t('head.update.userId'),
												name: 'updateUserId',
												width: '35%'
											}
										]
									}
								},

								gridSearchObj: {}
							};
						},
						mounted() {
							this.$nextTick(() => {
								this.search(this.$refs.search.getSearchObj());
							});
						},
						methods: {
							search(searchObj) {
								searchObj.equalSearch = true;
								searchObj.entityName = className;
								this.gridSearchObj = searchObj;
							},
							initialize() {
								this.$refs.search.initialize();
							},
							resize(gridObj) {
								if (!gridObj) return;

								this.$nextTick(() => {
									const modalBody = metaHistoryModalInstance.$el.querySelector(`.modal-body`);

									const modalBodyComputedStyle = getComputedStyle(modalBody);

									const modalBodyPaddingLeft = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-left'));
									const modalBodyPaddingRight = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-right'));
									const modalBodyPaddingTop = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-top'));
									const modalBodyPaddingBottom = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-bottom'));

									const adjustWidth = modalBody.clientWidth - modalBodyPaddingLeft - modalBodyPaddingRight;
									gridObj.invoke(`setWidth`, adjustWidth);

									const ctHeaderHeight = modalBody.querySelector(`.ct-header`).offsetHeight;
									const subBarHeight = modalBody.querySelector(`.sub-bar`).offsetHeight;

									const pagination = modalBody.querySelector(`.tui-grid-pagination`);
									const paginationComputedStyle = getComputedStyle(pagination);
									const paginationMarginTop = getNumFromStr(paginationComputedStyle.getPropertyValue('margin-top'));
									const paginationMarginBottom = getNumFromStr(paginationComputedStyle.getPropertyValue('margin-bottom'));

									const paginationHeight = pagination.offsetHeight;

									const adjustHeight =
										modalBody.clientHeight -
										modalBodyPaddingTop -
										modalBodyPaddingBottom -
										ctHeaderHeight -
										subBarHeight -
										paginationHeight -
										paginationMarginTop -
										paginationMarginBottom;

									gridObj.invoke(`setHeight`, adjustHeight);
								});
							},
							async rowClicked(info) {
								const {
									data: { object }
								} = await new HttpReq(`/api/entity/metaHistory/object`).read(info);

								const modifiedContentsModalInstance = this.$openModal({
									title: `Modified Contents`,
									modalWidth: `1280px`,
									modalHeight: `1000px`,
									contentList: [
										{
											template: `<div><Modified :detailData="detailData"/></div>`,
											components: {
												Modified: require('@/views/modal/metaHistory/Modified.vue').default
											},
											data() {
												return {
													detailData: null
												};
											},
											mounted() {
												this.detailData = {
													beforeDataString: object.beforeDataString,
													afterDataString: object.afterDataString
												};
											}
										}
									],
									buttonList: object.beforeDataString
										? [
												{
													template: `<button type="button" @click="restore" class="btn btn-primary">{{ $t('common.metaHistory.restore') }}</button>`,
													methods: {
														async restore() {
															const res = await new HttpReq('/api/entity/metaHistory/restore').create(object, true);

															if (`ok` === res.data.result) {
																this.$alert({ type: 'normal', message: this.$t(`head.restore.notice`) });

																_this.setData(
																	{
																		data: {
																			result: 'ok'
																		}
																	},
																	'head.update.notice'
																);

																metaHistoryModalInstance.hideModal();
																modifiedContentsModalInstance.hideModal();
															} else {
																this.$alert({ type: 'warn', message: this.$t('head.fail.notice') });
															}
														}
													}
												}
										  ]
										: []
								});
							}
						}
					}
				]
			});
		},
		goReferenceModal() {
			const { className } = this.buttonList.find(btn => 'reference' === btn.id);

			const detailInfo = {
				params: {
					className
				}
			};
			const detailData = this.detailData;

			const modalInstance = this.$openModal({
				title: this.$t(`head.referenceTreeInfo`),
				modalWidth: `900px`,
				modalHeight: `700px`,
				contentList: [
					{
						template: `<div>
						       	       <DetailPublishGrid :detailData="detailData" :detailInfo="detailInfo" type="modal" />
						   		   </div>`,
						components: {
							DetailPublishGrid: require('@/components/detail/DetailPublishGrid.vue').default
						},
						data() {
							return {
								detailInfo,
								detailData
							};
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" @click="download" class="btn btn-primary">{{ $t('head.excel.output') }}</button>`,
						methods: {
							download() {
								modalInstance.$refs.content[0].$children[0].exportExcel();
								modalInstance.hideModal();
							}
						}
					}
				]
			});
		}
	}
};
</script>
