									type: 'textarea',
									vModel: 'exceptionText',
									label: this.$t('igate.exceptionLog.exceptionText'),
									height: '100px'
								}
							]
						]
					]
				},
				{
					type: 'basic',
					label: this.$t('igate.exceptionLog.exceptionStack'),
					content: [
						[
							[
								{
									type: 'textarea',
									vModel: 'exceptionStack',
									label: this.$t('igate.exceptionLog.exceptionStack'),
									height: '300px'
								}
							]
						]
					]
				}
			]
		};
	}
};
</script>
