<template>
	<div>
		<div class="table-responsive">
			<TuiGrid ref="tuiGrid" v-bind="options" />
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import * as xlsx from 'xlsx';
import DetailGrid from '@/components/detail/DetailGrid';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';
import moment from 'moment';
import {
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	HttpReq,
	makeGridOptions,
	escapeHtml,
	changeDateFormat,
	openNewTab,
	getNumFromStr
} from '@/utils/common.js';

export default {
	name: 'DetailPublishGrid',
	extends: DetailGrid,
	props: {
		mode: String,
		detailData: Object,
		type: String
	},
	data() {
		return {
			options: {},
			params: {},
			HttpReq: null,
			resourceTypeList: {
				'com.inzent.igate.repository.meta.FieldMeta': {
					label: 'igate.fieldMeta',
					dataId: 'fieldId'
				},
				'com.inzent.igate.repository.meta.Adapter': {
					label: 'igate.adapter',
					dataId: 'adapterId',
					menuId: `202030`,
					searchData: `adapterId`
				},
				'com.inzent.igate.repository.meta.Interface': {
					label: 'igate.interface',
					dataId: 'interfaceId',
					menuId: `101050`,
					searchData: `interfaceId`
				},
				'com.inzent.igate.repository.meta.InterfacePolicy': {
					label: 'igate.interfacePolicy',
					isPkData: true,
					dataId: 'adapterId',
					menuId: `202030`,
					searchData: `adapterId`
				},
				'com.inzent.igate.repository.meta.InterfaceRecognize': {
					label: 'igate.interfaceRecognize',
					isPkData: true,
					dataId: 'adapterId',
					menuId: `101060`,
					searchData: `adapterId`
				},
				'com.inzent.igate.repository.meta.Job': {
					label: 'igate.job',
					dataId: 'jobId',
					menuId: `204010`,
					searchData: `jobId`
				},
				'com.inzent.igate.repository.meta.Mapping': {
					label: 'igate.mapping',
					dataId: 'mappingId',
					menuId: `101020`,
					searchData: `mappingId`
				},
				'com.inzent.igate.repository.meta.OnlineHeaderMappingPolicy': {
					label: 'igate.onlineHeaderMappingPolicy',
					isPkData: true,
					dataId: ['interfaceAdapterId', 'serviceAdapterId'],
					menuId: `203010`,
					searchData: ['pk.interfaceAdapterId', 'pk.serviceAdapterId'],
					searchLabel: ['igate.interface', 'igate.service']
				},
				'com.inzent.igate.repository.meta.Operation': {
					label: 'igate.operation',
					dataId: 'operationId',
					menuId: `102070`,
					searchData: `operationId`
				},
				'com.inzent.igate.repository.meta.Query': {
					label: 'igate.query',
					dataId: 'queryId',
					menuId: `101070`,
					searchData: `queryId`
				},
				'com.inzent.igate.repository.meta.Record': {
					label: 'igate.modelRecord',
					dataId: 'recordId',
					menuId: `101010`,
					searchData: `recordId`
				},
				'com.inzent.igate.repository.log.ReplyEmulate': {
					label: 'igate.service.replyemulate',
					isPkData: true,
					dataId: 'replyId'
				},
				'com.inzent.igate.repository.meta.Service': {
					label: 'igate.service',
					dataId: 'serviceId',
					menuId: `101030`,
					searchData: `serviceId`
				},
				'com.inzent.igate.repository.meta.ServicePolicy': {
					label: 'igate.servicePolicy',
					isPkData: true,
					dataId: 'adapterId',
					menuId: `202030`,
					searchData: `adapterId`
				},
				'com.inzent.igate.repository.meta.ServiceRecognize': {
					label: 'igate.serviceRecognize',
					isPkData: true,
					dataId: 'adapterId',
					menuId: `101040`,
					searchData: `adapterId`
				},
				'com.inzent.igate.repository.log.TestCase': {
					label: 'igate.testcase',
					isPkData: true,
					dataId: 'testCaseId'
				},
				'com.inzent.igate.repository.log.TestSuite': {
					label: 'igate.testsuite',
					dataId: 'testSuiteId'
				}
			}
		};
	},
	computed: {
		...mapState(['menuList'])
	},
	watch: {
		detailData() {
			this.initData();
		}
	},
	created() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);

		this.HttpReq = new HttpReq(`/api/reference/entity`);
		this.params = this.detailInfo.params;

		this.options = makeGridOptions({
			rowHeight: 30,
			minRowHeight: 30,
			treeColumnOptions: {
				name: 'className'
			},
			data: [],
			columns: [
				{
					name: 'resourceObject',
					hidden: true
				},
				{
					name: 'className',
					header: this.$t('head.publishLog.resource.type'),
					width: '25%',
					formatter: ({ row }) => {
						return this.$t(this.resourceTypeList[row.className].label);
					}
				},
				{
					name: 'resourceName',
					header: this.$t('head.name'),
					width: '35%',
					formatter: ({ row }) => {
						let resourceName = '';

						if (this.resourceTypeList[row.className].searchLabel) {
							this.resourceTypeList[row.className].searchLabel.forEach((label, idx) => {
								resourceName += `<span>${this.$t(label)}</span> <span ${0 === row.rowKey ? '' : 'class="link-new-tab"'} >${escapeHtml(
									row.resourceName.split(',')[idx].trim()
								)}</span> `;
							});
						} else
							resourceName = `<span ${
								0 === row.rowKey || row.className.startsWith(`com.inzent.igate.repository.log`) ? '' : 'class="link-new-tab"'
							} >${escapeHtml(row.resourceName)}</span>`;

						return resourceName;
					}
				},
				{
					name: `updateTimestamp`,
					header: this.$t('igate.notice.created.date'),
					width: '20%',
					align: `center`
				},
				{
					name: `updateUserId`,
					header: this.$t('igate.notice.writer'),
					width: '20%'
				}
			],
			onGridUpdated: evt => {
				evt.instance.expand(0, false);
			},
			onGridMounted: evt => {
				evt.instance.on('click', evt => {
					if (!evt.nativeEvent.target.classList.contains(`link-new-tab`)) return;

					const rowInfo = evt.instance.getRow(evt.rowKey);
					const resourceInfo = this.resourceTypeList[rowInfo.className];
					const menuInfo = this.menuList.filter(menuInfo => resourceInfo.menuId === menuInfo.menuId).pop();

					let multiResourceName = null,
						multiResourceIdx = null;

					if (-1 !== rowInfo.resourceName.indexOf(',')) {
						rowInfo.resourceName.split(',').forEach((data, idx) => {
							if (data.trim() === evt.nativeEvent.target.innerText) {
								multiResourceName = data.trim();
								multiResourceIdx = idx;
							}
						});
					}

					openNewTab(menuInfo.menuId, () => {
						localStorage.removeItem(`searchObj`);
						localStorage.removeItem(`detailObj`);

						localStorage.setItem(
							`searchObj`,
							JSON.stringify({
								[null !== multiResourceIdx ? resourceInfo.searchData[multiResourceIdx] : resourceInfo.searchData]:
									null !== multiResourceIdx ? multiResourceName : rowInfo.resourceName,
								_pageSize: '10'
							})
						);
						localStorage.setItem(`detailObj`, JSON.stringify(rowInfo.resourceObject));
					});
				});

				evt.instance.on('expand', () => {
					this.$nextTick(this.resize);
				});

				evt.instance.on('collapse', () => {
					this.$nextTick(this.resize);
				});
			}
		});
	},
	mounted() {
		this.initData();
	},
	methods: {
		async initData() {
			this.$refs.tuiGrid.invoke(`clear`);

			if (isEmpty(this.detailData)) return;

			const res = await this.HttpReq.read(
				{
					object: this.detailData,
					className: this.params.className
				},
				true
			);

			if ('ok' !== res.data.result) {
				this.$alert({ type: 'warn', message: `${res.data.error[0].message}` });
			} else {
				this.$refs.tuiGrid.invoke('resetData', [this.initTreeData(res.data.object)]);

				setTimeout(this.resize, 0);
			}
		},
		setResourceName(record, parentName) {
			const resourceTypeData = this.resourceTypeList[record.className];

			return `object` === typeof resourceTypeData.dataId
				? Object.values(record.object.pk).join(',')
				: record.className.startsWith(`com.inzent.igate.repository.log`)
				? `${parentName}@${record.object.pk[resourceTypeData.dataId]}`
				: resourceTypeData.isPkData
				? record.object.pk[resourceTypeData.dataId]
				: record.object[resourceTypeData.dataId];
		},
		initTreeData(record, parentName) {
			const treeData = {
				resourceObject: record.object,
				className: record.className,
				resourceName: this.setResourceName(record, parentName),
				updateUserId: record.object.updateUserId ? record.object.updateUserId : `-`,
				updateTimestamp: record.object.updateTimestamp ? changeDateFormat(record.object.updateTimestamp) : `-`
			};

			if (record.referencing && 0 < record.referencing.length) {
				treeData._children = [];

				record.referencing.forEach(field => {
					const subTreeData = {
						resourceObject: field.object,
						className: field.className,
						resourceName: this.setResourceName(field, treeData.resourceName),
						updateUserId: field.object.updateUserId ? field.object.updateUserId : `-`,
						updateTimestamp: field.object.updateTimestamp ? changeDateFormat(field.object.updateTimestamp) : `-`
					};

					if (field.referencing && 0 < field.referencing.length) {
						if (!subTreeData._children) {
							subTreeData._children = [];
						}

						field.referencing.forEach(reference => {
							subTreeData._children.push(this.initTreeData(reference, subTreeData.resourceName));
						});
					}

					treeData._children.push(subTreeData);
				});
			}

			return treeData;
		},
		exportExcel() {
			const headerInfo = {};

			this.$refs.tuiGrid.invoke('getColumns').forEach(({ name, header, hidden }) => {
				if (hidden) return;
				headerInfo[name] = header;
			});

			const workbook = xlsx.utils.book_new();

			const firstSheet = xlsx.utils.json_to_sheet(
				[
					headerInfo,
					...cloneDeep(this.$refs.tuiGrid.invoke('getData')).map(({ className, resourceName, updateUserId, updateTimestamp }) => ({
						className: this.$t(this.resourceTypeList[className].label),
						resourceName,
						updateUserId,
						updateTimestamp
					}))
				],
				{ header: Object.keys(headerInfo), skipHeader: true }
			);

			firstSheet['!cols'] = [{ wpx: 120 }, { wpx: 250 }, { wpx: 150 }, { wpx: 150 }];

			xlsx.utils.book_append_sheet(workbook, firstSheet, this.$t(`head.referenceTreeInfo`));

			xlsx.writeFile(workbook, `Reference_${moment(new Date()).format('YYYYMMDD_HHmmss')}.xlsx`);
		},
		resize() {
			if (!this.$refs.tuiGrid) return;

			if (`modal` === this.type) {
				const modalBody = document.querySelector(`.modal-body`);

				const modalBodyComputedStyle = getComputedStyle(modalBody);

				const modalBodyPaddingLeft = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-left'));
				const modalBodyPaddingRight = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-right'));
				const modalBodyPaddingTop = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-top'));
				const modalBodyPaddingBottom = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-bottom'));

				this.$refs.tuiGrid.invoke('setWidth', modalBody.clientWidth - modalBodyPaddingLeft - modalBodyPaddingRight);
				this.$refs.tuiGrid.invoke('setHeight', modalBody.clientHeight - modalBodyPaddingTop - modalBodyPaddingBottom);
			} else {
				const panel = document.getElementById('panel');

				const panelBody = panel.getElementsByClassName('panel-body')[0];
				const panelBodyComputedStyle = getComputedStyle(panelBody);

				const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
				const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));
				const panelBodyPaddingTop = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-top'));
				const panelBodyPaddingBottom = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-bottom'));

				this.$refs.tuiGrid.invoke('setWidth', panelBody.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight);
				this.$refs.tuiGrid.invoke('setHeight', panelBody.clientHeight - panelBodyPaddingTop - panelBodyPaddingBottom);
			}
		}
	}
};
</script>
