<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.id') }}</span>
						<span class="letterLength" v-if="!getReadonly(regExpInfo[`recordId`])">
							{{ `(${curLengthCnt['recordId']}/${maxLengthCnt['recordId']})` }}
						</span>
						<b class="icon-star"></b>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`recordId`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`recordId`]) ? '8px' : '0px'}` }"
					>
						<inputText
							ref="recordId"
							vModel="recordId"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`recordId`]),
								'regExp-text': !getReadonly(regExpInfo[`recordId`]),
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly || getReadonly(regExpInfo[`recordId`])"
							regExpType="id"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.name') }}</span>
						<span class="letterLength" v-if="!getReadonly(regExpInfo[`recordName`])">
							{{ `(${curLengthCnt['recordName']}/${maxLengthCnt['recordName']})` }}
						</span>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`recordName`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`recordName`]) ? '8px' : '0px'}` }"
					>
						<inputText
							ref="recordName"
							vModel="recordName"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`recordName`]),
								'regExp-text': !getReadonly(regExpInfo[`recordName`]),
								'view-disabled': true,
								dataKey: false
							}"
							:readonly="readonly || getReadonly(regExpInfo[`recordName`])"
							regExpType="name"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>Private</span>
						<b class="icon-star"></b>
					</label>
					<selectBox
						ref="privateYn"
						vModel="privateYn"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: '/api/page/properties',
							params: {
								pk: {
									propertyId: 'List.Yn'
								},
								orderByKey: true
							},
							optionListName: 'privateYnList',
							optionFor: 'option in privateYnList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.type') }}</span>
						<b class="icon-star"></b>
					</label>
					<select
						ref="recordType"
						v-model="recordType"
						class="form-control view-disabled"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
					>
						<option
							v-for="(recordTypeObj, index) in recordTypeArr"
							:key="index"
							:value="recordTypeObj.pk.propertyKey"
							:disabled="recordTypeObj.disabled"
						>
							{{ recordTypeFormatter(recordTypeObj.pk.propertyKey) }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.record.metaDomain') }}</span>
					</label>
					<selectBox
						ref="metaDomain"
						vModel="metaDomain"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: '/api/entity/fieldMeta/group/search',
							params: null,
							optionListName: 'metaDomainList',
							optionFor: 'option in metaDomainList',
							optionValue: 'option',
							optionText: 'option'
						}"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.group') }}</span>
						<b class="icon-star"></b>
					</label>
					<inputText
						ref="recordGroup"
						vModel="recordGroup"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
						:readonly="readonly || getReadonly({ isPK: true, readonly: false })"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.privilege') }}</span>
						<b class="icon-star"></b>
					</label>
					<select
						ref="privilegeId"
						v-model="privilegeId"
						class="form-control view-disabled"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
					>
						<option v-for="option in privilegeTypeList" :key="option.privilegeId" :value="option.privilegeId" :disabled="option.disabled">
							{{ option.privilegeId }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.description') }}</span>
						<span class="letterLength" v-if="!getReadonly(regExpInfo[`recordDesc`])">
							{{ `(${curLengthCnt['recordDesc']}/${maxLengthCnt['recordDesc']})` }}
						</span>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`recordDesc`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`recordDesc`]) ? '8px' : '0px'}` }"
					>
						<textArea
							ref="recordDesc"
							vModel="recordDesc"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`recordDesc`]),
								'regExp-textarea': !getReadonly(regExpInfo[`recordDesc`]),
								'view-disabled': true
							}"
							:readonly="readonly || getReadonly(regExpInfo[`recordDesc`])"
							:height="'130px'"
							regExpType="desc"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import merge from 'lodash/merge';
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { parseHierarchyObj, HttpReq, getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'DetailBasic',
	extends: DetailBasic,
	data() {
		return {
			privilegeTypeList: [],
			privilegeId: null,
			recordTypeArr: [],
			recordType: null,

			keyArr: ['recordId', 'recordName', 'privilegeId', 'privateYn', 'metaDomain', 'recordType', 'recordGroup', 'recordDesc'],

			regExpInfo: {
				recordId: {
					regExpType: 'id',
					isPK: true,
					readonly: false
				},
				recordName: {
					regExpType: 'name',
					isPK: false,
					readonly: false
				},
				recordDesc: {
					regExpType: 'desc',
					isPK: false,
					readonly: false
				}
			}
		};
	},
	async created() {
		let httpReq = new HttpReq(`/api/page/properties`);

		let result = await httpReq.read({ pk: { propertyId: 'List.Record.Type' }, orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.recordTypeArr = result.data.object.map(info => {
			info.disabled = !('H' === info.pk.propertyKey || 'R' === info.pk.propertyKey);
			return info;
		});

		httpReq = new HttpReq(`/api/entity/privilege/search`);

		result = await httpReq.read({
			object: {
				privilegeType: `b`
			},
			limit: null,
			next: null,
			reverseOrder: false
		});

		if (`ok` !== result.data.result) return;

		const privilegeTypeList = result.data.object;

		const bTotalUserPrivileges = this.userInfo.totalUserPrivileges.filter(userPrivilege => `b` === userPrivilege.privilegeType);

		for (const info of privilegeTypeList) {
			if (this.isUserAdmin) {
				info.disabled = false;
			} else {
				info.disabled = `undefined` === typeof bTotalUserPrivileges.find(userPrivilege => userPrivilege.privilegeId == info.privilegeId);
			}
		}

		this.privilegeTypeList = privilegeTypeList;
	},
	computed: {
		...mapState(['userInfo', 'regExpTypeList']),
		...mapGetters(['isUserAdmin'])
	},
	methods: {
		initData() {
			const detailDataFormalize = {};

			this.keyArr.forEach(key => {
				detailDataFormalize[key] =
					'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;

				const regExpType = !Object.keys(this.regExpInfo).includes(key) ? 'default' : this.regExpInfo[key].regExpType;

				this.setLengthCnt({ [key]: 0 });
				this.maxLengthCnt[key] = getRegExpInfo(regExpType).maxLength;
			});

			for (const key in this.$refs) {
				if (!this.$refs[key].setData) continue;
				this.$refs[key].setData(detailDataFormalize[this.$refs[key]._props.vModel]);
			}

			this.privilegeId = detailDataFormalize.privilegeId;
			this.recordType = detailDataFormalize.recordType;
		},
		getData() {
			let rtnObj = {};

			for (let key in this.$refs) {
				if (0 === this.$refs[key].length) continue;

				if ('recordType' === key) rtnObj.recordType = this.recordType;
				else if (`privilegeId` === key) rtnObj.privilegeId = this.privilegeId;
				else rtnObj = merge(rtnObj, this.$refs[key].getData());
			}

			return parseHierarchyObj(rtnObj);
		},
		recordTypeFormatter(recordType) {
			switch (recordType) {
				case 'C':
					return 'Common data-model';
				case 'H':
					return 'Header data-model';
				case 'R':
					return 'Reference data-model';
				case 'I':
					return 'Individual data-model';
				case 'E':
					return 'Embedded data-model';
			}
		}
	}
};
</script>
