<template>
	<div>
		<div class="row frm-row" v-for="(contentRow, contentRowIdx) in detailInfo.content" :key="contentRowIdx">
			<div :class="'col-lg-' + 12 / contentRow.length" v-for="(contentCol, contentColIdx) in contentRow" :key="contentColIdx">
				<div class="form-group" v-for="(colInfo, colInfoIdx) in contentCol" :key="colInfoIdx">
					<label class="control-label">
						<span>
							{{ colInfo.label }}
							<span class="letterLength" v-if="regExpTypeList.includes(colInfo.type) && !getReadonly(colInfo)">
								{{ `(${curLengthCnt[colInfo.vModel]}/${maxLengthCnt[colInfo.vModel]})` }}
							</span>
						</span>
						<b class="icon-star" v-if="colInfo.isPK || colInfo.isRequired"></b>
					</label>
					<label class="control-label warningLabel" v-if="colInfo.warning && !readonly">
						<span>{{ colInfo.warningLabel }}</span>
					</label>
					<div
						:class="`detail-content-${regExpTypeList.includes(colInfo.type) && !getReadonly(colInfo) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${regExpTypeList.includes(colInfo.type) && !getReadonly(colInfo) ? '8px' : '0px'}` }"
					>
						<inputText
							:ref="`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{
								'form-control': getReadonly(colInfo),
								[`regExp-${colInfo.type}`]: !getReadonly(colInfo),
								'view-disabled': true,
								dataKey: colInfo.isPK
							}"
							:readonly="readonly || getReadonly(colInfo)"
							:regExpType="colInfo.regExpType"
							:clickEvtType="colInfo.clickEvtType"
							:clickEvt="colInfo.clickEvt"
							:changeEvt="colInfo.changeEvt"
							@setLengthCnt="setLengthCnt"
							v-if="'text' === colInfo.type"
						/>
						<selectBox
							:ref="`selectBox_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly || getReadonly(colInfo)"
							:optionInfo="colInfo.optionInfo"
							v-else-if="'select' === colInfo.type"
						/>
						<dataList
							:ref="`dataList_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{
								'form-control': getReadonly(colInfo),
								[`regExp-${colInfo.type}`]: !getReadonly(colInfo),
								'view-disabled': true
							}"
							:readonly="readonly || getReadonly(colInfo)"
							:optionInfo="colInfo.optionInfo"
							:regExpType="colInfo.regExpType"
							@setLengthCnt="setLengthCnt"
							v-else-if="'dataList' === colInfo.type"
						/>
						<textArea
							:ref="`textArea_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{
								'form-control': getReadonly(colInfo),
								[`regExp-${colInfo.type}`]: !getReadonly(colInfo),
								'view-disabled': true
							}"
							:readonly="readonly || getReadonly(colInfo)"
							:height="colInfo.height"
							:regExpType="colInfo.regExpType"
							@setLengthCnt="setLengthCnt"
							v-else-if="'textarea' === colInfo.type"
						/>
						<radioGroup
							:ref="`radioGroup_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{ 'custom-control': true, 'custom-radio single': true, 'view-disabled': true }"
							:readonly="readonly || getReadonly(colInfo)"
							:radioInfo="colInfo.radioInfo"
							v-else-if="'radio' === colInfo.type"
						/>
						<checkBoxGroup
							:ref="`checkBoxGroup_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:checkboxInfo="colInfo.checkboxInfo"
							:optionListData="this.transactionList"
							:classObject="{ 'custom-control': true, 'custom-checkbox single': true, 'view-disabled': true }"
							v-else-if="'checkBox' === colInfo.type"
						/>
						<dateRange
							:ref="`dateRange_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:readonly="readonly || getReadonly(colInfo)"
							:customOptions="colInfo.options"
							v-else-if="'singleDaterange' === colInfo.type"
						/>
						<timePicker
							:ref="`timePicker_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{ 'form-control': true }"
							:readonly="readonly || getReadonly(colInfo)"
							v-if="'time' === colInfo.type"
						/>
						<div class="input-group" v-else-if="'password' === colInfo.type" style="flex-wrap: nowrap">
							<passWord
								:ref="`passWord_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
								:vModel="colInfo.vModel"
								:classObject="{
									'form-control': getReadonly(colInfo),
									[`regExp-${colInfo.type}`]: !getReadonly(colInfo),
									'view-disabled': true
								}"
								:readonly="readonly || getReadonly(colInfo)"
								:regExpType="colInfo.regExpType"
								@setLengthCnt="setLengthCnt"
							/>
							<i
								class="icon-eye password-eye"
								@click="$refs[`passWord_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`][0].changeType()"
								v-if="!readonly"
							></i>
						</div>
						<div class="input-group" v-else-if="'modal' === colInfo.type">
							<inputText
								:ref="`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
								:vModel="colInfo.vModel"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: colInfo.isPK }"
								:readonly="true"
								:clickEvtType="colInfo.clickEvtType"
								:clickEvt="colInfo.clickEvt"
							/>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="openModal(colInfo.modalInfo, $refs[`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`])"
								v-if="!(readonly || getReadonly(colInfo))"
							>
								<i class="icon-srch"></i>{{ $t('label.search') }}
							</button>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="$refs[`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`][0].setData(null)"
								v-if="!(readonly || getReadonly(colInfo))"
								:title="$t('head.initialize')"
							>
								<i class="icon-reset" style="margin-right: 0px"></i>
							</button>
						</div>
					</div>
				</div>
			</div>
		</div>
		<component :is="relBasicInfo" :detailData="detailData" />
	</div>
</template>

<script>
import { Grid } from '@toast-ui/vue-grid';
import DetailBasic from '@/components/detail/DetailBasic';
import { HttpReq, makeGridOptions, getNumFromStr, parseFlattenObj, numberWithComma, openNewTab, getCurrentMenuId } from '@/utils/common.js';

export default {
	name: 'DetailBasic',
	extends: DetailBasic,
	data() {
		return {
			relBasicInfo: {
				template: `
                    <TuiGrid v-bind="options" v-if="isGridInit" ref="tuiGrid" />                
                `,
				props: {
					detailData: Object
				},
				components: {
					TuiGrid: Grid
				},
				data() {
					return {
						isCreated: false,
						fileStatusList: [],
						options: null,
						isGridInit: false
					};
				},
				watch: {
					detailData: {
						deep: true,
						async handler() {
							if (!this.detailData.transactionId) return;

							if (!this.isGridInit) this.initGrid();

							if (0 === this.fileStatusList.length) {
								const res = await new HttpReq(`/api/page/properties`).read({
									pk: {
										propertyId: `List.FileRepository.Status`
									},
									orderByKey: true
								});

								this.fileStatusList = res.data.object;
							}

							const res = await new HttpReq(`/api/entity/fileRepository/search`).read({
								object: {
									transactionId: this.detailData.transactionId
								},
								limit: null,
								next: null,
								reverseOrder: false
							});

							this.$nextTick(() => {
								this.$refs.tuiGrid.invoke(
									'resetData',
									res.data.object.map(item => parseFlattenObj(item))
								);

								this.resize();
							});
						}
					}
				},
				methods: {
					initGrid() {
						this.options = makeGridOptions({
							columns: [
								{
									name: 'createDateTime',
									header: `${this.$t('igate.connectorControl.create')} ${this.$t('head.date')}`,
									align: 'center',
									formatter: obj => obj.value.substring(0, 19)
								},
								{
									name: 'pk.fileMode',
									header: `${this.$t('head.file')} ${this.$t('head.mode')}`,
									align: 'center',
									formatter: ({ value }) => (`R` === value ? this.$t('head.recv') : this.$t('head.send'))
								},
								{
									name: 'pk.fileName',
									header: `${this.$t('head.file')} ${this.$t('head.name')}`
								},
								{
									name: 'fileStatus',
									header: `${this.$t('head.file')} ${this.$t('head.status')}`,
									align: 'center',
									formatter: ({ value }) => this.fileStatusList.find(item => item.pk.propertyKey === value).propertyValue
								},
								{
									name: 'pk.fileDate',
									header: `${this.$t('head.file')} ${this.$t('head.date')}`,
									align: 'center'
								},
								{
									name: 'pk.adapterId',
									header: `${this.$t('igate.adapter')} ${this.$t('head.id')}`
								},
								{
									name: 'interfaceServiceId',
									header: `${this.$t('igate.interface')} ${this.$t('igate.service')} ${this.$t('head.id')}`
								},
								{
									name: 'fileLength',
									header: `${this.$t('head.file')} ${this.$t('head.length')}`,
									align: 'right',
									formatter: info => {
										const number = info.row.fileLength;
										return number ? numberWithComma(number) : '';
									}
								}
							],
							onGridMounted: evt => {
								evt.instance.on(`click`, evt => {
									if (`undefined` === typeof evt.rowKey) return;

									openNewTab(getCurrentMenuId(), () => {
										localStorage.removeItem(`detailObj`);
										localStorage.setItem(`detailObj`, JSON.stringify(evt.instance.getRow(evt.rowKey)));
									});
								});
							}
						});

						this.isGridInit = true;
					},
					resize() {
						if (!this.$refs.tuiGrid) return;

						const panel = document.getElementById('panel');

						const panelBody = panel.getElementsByClassName('panel-body')[0];
						const panelBodyComputedStyle = getComputedStyle(panelBody);

						const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
						const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));

						this.$refs.tuiGrid.invoke('setWidth', panelBody.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight);
						this.$refs.tuiGrid.invoke('setHeight', `600px`);
					}
				}
			}
		};
	}
};
</script>
