<script>
import Basic from '@/components/main/Basic.vue';
import Main from '@/views/main/batchLog/Main.vue';
import Detail from '@/views/detail/batchLog/Detail.vue';
import { getPrivilege, changeDateFormat, HttpReq, escapeHtml, openNewTab } from '@/utils/common.js';
import { mapState } from 'vuex';

export default {
	name: 'BatchLog',
	extends: Basic,
	components: {
		Main,
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;

		const columns = [
			{
				name: 'transactionId',
				header: `${this.$t('head.transaction')} ${this.$t('head.id')}`,
				width: '20%'
			},
			{
				name: 'logCode',
				header: `${this.$t('head.log')} ${this.$t('head.classification')}`,
				align: 'center',
				width: '5%'
			},
			{
				name: 'adapterId',
				header: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
				width: '5%'
			},
			{
				name: 'interfaceServiceId',
				header: `${this.$t('igate.interface')} ${this.$t('igate.service')} ${this.$t('head.id')}`,
				width: '10%'
			},
			{
				name: 'exceptionCode',
				header: this.$t('igate.exceptionCode'),
				align: 'center',
				width: '10%'
			},
			{
				name: 'startTimestamp',
				header: this.$t('igate.traceLog.requestTimestamp'),
				align: 'center',
				width: '12%',
				formatter: obj => changeDateFormat(obj.value)
			},
			{
				name: 'endTimestamp',
				header: this.$t('igate.traceLog.responseTimestamp'),
				align: 'center',
				width: '12%',
				formatter: obj => changeDateFormat(obj.value)
			},
			{
				name: 'instanceId',
				header: `${this.$t('igate.instance')} ${this.$t('head.id')}`,
				width: '6%'
			},
			{
				name: 'connectorId',
				header: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
				width: '10%'
			},
			{
				name: 'safMessage',
				header: this.$t('igate.batchLog.retryInfo'),
				align: 'center',
				width: '5%',
				formatter: ({ row }) => {
					if (row.exceptionCode && this.hasNewTabPrivilege(`SafMessageViewer`)) {
						return `<div><a type="button" class="btn-m icon-srch"></a></div>`;
					} else {
						return `<div><a>-</a></div>`;
					}
				}
			},
			{
				name: 'traceLog',
				header: this.$t('igate.batchLog.logInfo'),
				align: 'center',
				width: '5%',
				formatter: () => {
					if (this.hasNewTabPrivilege(`TraceLogViewer`)) {
						return `<div><a type="button" class="btn-m icon-srch"></a></div>`;
					} else {
						return `<div><a>-</a></div>`;
					}
				}
			}
		];

		if (this.isTools) {
			columns.splice(columns.length - 2, 2);

			columns[3].width = '15%';
			columns[8].width = '15%';
		}

		this.gridInfo.options.columns = columns;
	},
	async mounted() {
		const result = await new HttpReq(`/api/page/properties`).read({ pk: { propertyId: 'List.Instance.InstanceType' }, orderByKey: true });

		if (`ok` === result.data.result) this.instanceTypeList = result.data.object;

		this.initNewTabSearchObj();
	},
	computed: {
		...mapState(['userInfo', 'isTools'])
	},
	methods: {
		hasNewTabPrivilege(privilegeId) {
			return this.userInfo.totalUserPrivileges.find(userPrivilegeInfo => privilegeId === userPrivilegeInfo.privilegeId);
		},
		rowClicked(info) {
			this.detailTabList[0].content[0][0][0].clickEvt = !info.exceptionCode
				? null
				: component => {
						openNewTab(`103010`, () => {
							localStorage.removeItem('searchObj');
							localStorage.setItem('searchObj', JSON.stringify({ transactionId: component.getData().transactionId }));
						});
				  };

			this.initDetail('detail', info);
		}
	},
	data() {
		return {
			instanceTypeList: [],

			cudUrl: '/api/entity/batchLog/object',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: 'singleDaterange', vModel: 'fromLogDateTime', label: this.$t('head.from'), dateRangeType: 'from' },
				{ type: 'singleDaterange', vModel: 'toLogDateTime', label: this.$t('head.to'), dateRangeType: 'to' },
				{
					type: 'text',
					vModel: 'transactionId',
					label: `${this.$t('head.transaction')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'logCode',
					label: `${this.$t('head.log')} ${this.$t('head.classification')}`,
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'modal',
					modalInfo: {
						title: this.$t('igate.instance'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'instanceId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'instanceNode',
									label: this.$t('igate.instance.node'),
									placeholder: this.$t('head.searchData')
								},
								{
									type: 'select',
									vModel: 'instanceType',
									label: this.$t('common.type'),
									val: `T`,
									optionInfo: {
										url: '/api/page/properties',
										params: {
											pk: {
												propertyId: 'List.Instance.InstanceType'
											},
											orderByKey: true
										},
										optionListName: 'instanceTypes',
										optionFor: 'option in instanceTypes',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue',
										optionIf: `'T' === option.pk.propertyKey`,
										isViewAll: false
									}
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/instance/search`,
							totalCntUrl: `/api/entity/instance/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										name: 'instanceId',
										header: this.$t('head.id'),
										width: '25%'
									},
									{
										name: 'instanceType',
										header: this.$t('common.type'),
										width: '25%',
										formatter: ({ row }) => {
											const findInstanceType = this.instanceTypeList.find(info => info.pk.propertyKey === row.instanceType);
											return findInstanceType ? escapeHtml(findInstanceType.propertyValue) : '';
										}
									},
									{
										name: 'instanceAddress',
										header: this.$t('igate.instance.address'),
										width: '25%'
									},
									{
										name: 'instanceNode',
										header: this.$t('igate.instance.node'),
										width: '25%'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => rowInfo.instanceId
					},
					vModel: 'instanceId',
					label: `${this.$t('igate.instance')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'modal',
					vModel: 'adapterId',
					label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.adapter'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'adapterId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'adapterName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'text',
									vModel: 'adapterDesc',
									label: this.$t('head.description'),
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/adapter/search`,
							totalCntUrl: `/api/entity/adapter/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										name: 'adapterId',
										header: this.$t('head.id'),
										width: '20%'
									},
									{
										name: 'adapterName',
										header: this.$t('head.name'),
										width: '20%'
									},
									{
										name: 'adapterDesc',
										header: this.$t('head.description'),
										width: '20%'
									},
									{
										name: 'requestStructure',
										header: this.$t('igate.adapter.structure.request'),
										width: '20%'
									},
									{
										name: 'responseStructure',
										header: this.$t('igate.adapter.structure.response'),
										width: '20%'
									}
								]
							}
						},
						rowClickedCallback: function (rowInfo) {
							return rowInfo.adapterId;
						}
					}
				},
				{
					type: 'modal',
					vModel: 'connectorId',
					label: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.connector'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'connectorId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'connectorName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/connector/search`,
							totalCntUrl: `/api/entity/connector/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										name: 'connectorId',
										header: this.$t('head.id'),
										width: '25%'
									},
									{
										name: 'connectorName',
										header: this.$t('head.name'),
										width: '25%'
									},
									{
										name: 'connectorDesc',
										header: this.$t('head.description'),
										width: '25%'
									},
									{
										name: 'connectorType',
										header: this.$t('common.type'),
										width: '25%'
									}
								]
							}
						},
						rowClickedCallback: function (rowInfo) {
							return rowInfo.connectorId;
						}
					}
				},
				{
					type: 'text',
					vModel: 'sessionId',
					label: `${this.$t('igate.connectorControl.session')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'remoteAddr',
					label: this.$t(`head.ip`),
					placeholder: this.$t('head.searchId'),
					regExpType: 'ip'
				},
				{
					type: 'modal',
					vModel: 'interfaceServiceId',
					label: `${this.$t('igate.interface')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.interface'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'interfaceId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'interfaceName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/interface/search`,
							totalCntUrl: `/api/entity/interface/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('head.id'),
										name: 'interfaceId'
									},
									{
										header: this.$t('head.name'),
										name: 'interfaceName'
									},
									{
										header: this.$t('head.description'),
										name: 'interfaceDesc'
									},
									{
										header: this.$t('common.type'),
										name: 'interfaceType'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => {
							return rowInfo.interfaceId;
						}
					}
				},
				{
					type: 'modal',
					vModel: 'interfaceServiceId',
					label: `${this.$t('igate.service')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.service'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'serviceId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'serviceName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/service/search`,
							totalCntUrl: `/api/entity/service/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										name: 'serviceId',
										header: this.$t('head.id'),
										width: '25%'
									},
									{
										name: 'serviceName',
										header: this.$t('head.name'),
										width: '25%'
									},
									{
										name: 'serviceDesc',
										header: this.$t('head.description'),
										width: '25%'
									},
									{
										name: 'serviceType',
										header: this.$t('common.type'),
										width: '25%'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => rowInfo.serviceId
					}
				},
				{
					type: 'singleDate',
					vModel: 'fileDate',
					label: `${this.$t('head.file')} ${this.$t('head.date')}`,
					placeholder: this.$t('head.searchFileStamp')
				},
				{
					type: 'text',
					vModel: 'fileName',
					label: `${this.$t('head.file')} ${this.$t('head.name')}`,
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'text',
					vModel: 'tableLastKey',
					label: this.$t('igate.batchLog.tableLastKey'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					placeholder: this.$t('head.searchCoditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'refresh', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/batchLog/search`,
				totalCntUrl: `/api/entity/batchLog/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: null
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['pk.logId', 'pk.logDateTime'],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [],
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('head.basic.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'transactionId',
									label: `${this.$t('head.transaction')} ${this.$t('head.id')}`,
									clickEvt: component => {
										openNewTab(`103010`, () => {
											localStorage.removeItem('searchObj');
											localStorage.setItem('searchObj', JSON.stringify({ transactionId: component.getData().transactionId }));
										});
									}
								},
								{
									type: 'text',
									vModel: `adapterId`,
									label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
									clickEvt: component => {
										openNewTab(`202030`, () => {
											localStorage.removeItem('searchObj');
											localStorage.setItem('searchObj', JSON.stringify({ adapterId: component.getData().adapterId }));
										});
									}
								},
								{
									type: 'text',
									vModel: 'remoteAddr',
									label: this.$t('common.metaHistory.updateRemoteAddress')
								},
								{
									type: 'text',
									vModel: 'fileName',
									label: `${this.$t('head.file')} ${this.$t('head.name')}`
								},
								{
									type: 'text',
									vModel: 'tableLastKey',
									label: this.$t('igate.batchLog.tableLastKey')
								},
								{
									type: 'text',
									vModel: 'exceptionMessage',
									label: this.$t('head.exception.message')
								}
							],
							[
								{
									type: 'text',
									vModel: 'logCode',
									label: `${this.$t('head.log')} ${this.$t('head.classification')}`
								},
								{
									type: 'text',
									vModel: `connectorId`,
									label: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
									clickEvt: component => {
										openNewTab(`202020`, () => {
											localStorage.removeItem('searchObj');
											localStorage.setItem('searchObj', JSON.stringify({ connectorId: component.getData().connectorId }));
										});
									}
								},
								{
									type: 'text',
									vModel: 'interfaceServiceId',
									label: `${this.$t('igate.interface')} ${this.$t('igate.service')} ${this.$t('head.id')}`
								},
								{
									type: 'text',
									vModel: 'fileSequence',
									label: `${this.$t('head.file')} ${this.$t('head.sequence')}`
								},
								{
									type: 'text',
									vModel: 'safId',
									label: `${this.$t('igate.saf')} ${this.$t('head.id')}`,
									clickEvt: component => {
										openNewTab(`103060`, () => {
											localStorage.removeItem('searchObj');
											localStorage.setItem('searchObj', JSON.stringify({ 'pk.safId': component.getData().safId }));
										});
									}
								},
								{
									type: 'text',
									vModel: 'startTimestamp',
									label: this.$t('igate.transactionRestriction.time.start'),
									formatter: val => changeDateFormat(val)
								}
							],
							[
								{
									type: 'text',
									vModel: 'instanceId',
									label: `${this.$t('igate.instance')} ${this.$t('head.id')}`
								},
								{
									type: 'text',
									vModel: `sessionId`,
									label: `${this.$t('igate.connectorControl.session')} ${this.$t('head.id')}`
								},
								{
									type: 'text',
									vModel: 'fileDate',
									label: `${this.$t('head.file')} ${this.$t('head.date')}`
								},
								{
									type: 'text',
									vModel: 'tableSequence',
									label: `${this.$t('head.table')} ${this.$t('head.sequence')}`
								},
								{
									type: 'text',
									vModel: 'exceptionCode',
									label: this.$t('igate.exceptionCode')
								},
								{
									type: 'text',
									vModel: 'endTimestamp',
									label: this.$t('igate.transactionRestriction.time.end'),
									formatter: val => changeDateFormat(val)
								}
							]
						]
					]
				}
			]
		};
	}
};
</script>
