<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/modelRecord/Detail.vue';
import { getPrivilege, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'ModelRecord',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}

		if (localStorage.getItem('detailObj')) {
			this.$nextTick(() => {
				const detailObj = JSON.parse(localStorage.getItem(`detailObj`));
				detailObj.isExpandDetailView = true;

				localStorage.removeItem(`detailObj`);

				this.initDetail('detail', detailObj);
			});
		}
	},
	data() {
		return {
			cudUrl: '/api/entity/record/object',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'recordId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'recordName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'select',
					vModel: 'recordType',
					label: this.$t('common.type'),
					val: `c`,
					optionInfo: {
						url: '/api/page/properties',
						params: {
							pk: {
								propertyId: 'List.Record.Type'
							},
							orderByKey: false
						},
						optionListName: 'recordTypes',
						optionFor: 'option in recordTypes',
						optionValue: 'option.pk.propertyKey',
						optionText: `'c' === option.pk.propertyKey? '${this.$t(`head.all`)}' : option.propertyValue`,
						optionIf: `option.pk.propertyKey != 'E' && option.pk.propertyKey != 'I'`,
						isViewAll: false
					}
				},
				{
					type: 'text',
					vModel: 'recordGroup',
					label: this.$t(`head.group`),
					placeholder: this.$t('head.searchData'),
					regExpType: 'name'
				},
				{
					type: 'dataList',
					vModel: 'privilegeId',
					label: this.$t(`common.privilege`),
					placeholder: this.$t('head.searchData'),
					regExpType: 'searchId',
					optionInfo: {
						url: '/api/entity/privilege/search',
						params: {
							object: {
								privilegeType: `b`
							},
							limit: null,
							next: null,
							reverseOrder: false
						},
						optionListName: 'privilegeTypeList',
						optionFor: 'option in privilegeTypeList',
						optionValue: 'option.privilegeId',
						optionText: 'option.privilegeId'
					}
				},
				{
					type: 'text',
					vModel: 'recordDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'newTab', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'excelExport', isUse: true, exportUrl: '/api/entity/record/download', fileName: `Record List Export` }
				// { id: `excelImport`, isUse: true, importUrl: `/api/entity/record/document` },
				// { id: 'add', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/record/search`,
				totalCntUrl: `/api/entity/record/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('head.id'),
							name: 'recordId',
							width: '15%'
						},
						{
							header: this.$t('head.name'),
							name: 'recordName',
							width: '20%'
						},
						{
							header: this.$t('common.type'),
							name: 'recordType',
							align: 'center',
							width: '10%',
							formatter: ({ value }) =>
								`H` === value ? `header` : `I` === value ? `indivi` : `R` === value ? `refer` : `c` === value ? `common` : `embed`
						},
						{
							header: this.$t('head.group'),
							name: 'recordGroup',
							width: '20%'
						},
						{
							header: this.$t('common.privilege'),
							name: 'privilegeId',
							width: '10%'
						},
						{
							header: this.$t('head.description'),
							name: 'recordDesc',
							width: '25%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//detail
			detailPk: ['recordId'],
			controlUrl: '/api/entity/record/control',
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'dump', isUse: true, dumpUrl: `/api/entity/record/dump` },
				{ id: 'excelExport', isUse: true, exportUrl: '/api/entity/record/document', fileName: `Record Object Export` },
				{ id: 'reference', isUse: true, className: 'com.inzent.igate.repository.meta.Record' },
				{ id: 'metaHistory', isUse: true, className: 'com.inzent.igate.repository.meta.Record', entityIdKey: `recordId` }
			],
			controlParams: detailData => ({
				recordId: detailData.recordId
			}),
			detailTabList: [
				{ type: 'bundle', url: '/modelRecord/BasicInfo', label: this.$t('head.basic.info') },
				{ type: 'bundle', url: '/modelRecord/DetailInfo', label: this.$t('head.model.info') },
				{ type: 'bundle', url: '/modelRecord/ExtendInfo', label: this.$t('head.extension.info') },
				{
					id: `useInfo`,
					type: 'basic',
					label: this.$t('head.resource.inuse.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'lockUserId',
									label: this.$t('head.lock.userId'),
									readonly: true,
									val: '0'
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: this.$t('head.updateVersion'),
									readonly: true,
									val: '0'
								}
							],
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('head.update.userId'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('head.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	}
};
</script>
